	module.exports = {"data":{"markdownRemark":{"html":"<p>The Jinjiang Small Hall meeting established the first meeting of the Joint Conference on Human Resources Development in the Yangtze River Basin. The personnel institutions of 28 cities in the region set up a collaborative organization. Such an organization has a positive meaning, and human resources are the first resource. Such an idea is not a solid concept in China. This has gradually established such a concept in recent years, but China has always been a country with a surplus population. People are the most, so people are not precious. Mao Zedong said that people are the first to be valuable. However, such an idea was not established in that era. The current economic development requires more talents. The development of science and technology is an irresistible force. Without talents, society cannot move forward quickly. Whether it is competition now, international competition or domestic competition, corporate competition or academic competition, the last thing is talent. No one has nothing.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0071.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0071.md","frontmatter":{"title":"April 8th Friday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0071.md absPath of file >>> MarkdownRemark"}}

